import Slider from "./Slider.js"

export default class Volume extends Slider {

  constructor(props, context) {
    super(props, context)
    this.classList.add("amp-volume", true)
    this.state.min = 0
    this.state.max = 100
    this.state.percent = this.player.volume
    this.state.value = this.player.volume * 100
    this.state.altText = "MSG_VOLUME"
  }

  static get contextTypes() {
    return super.contextTypes
  }

  format(value) {
    return Math.round(value)
  }

  onvolumechange(event) {
    this.update({percent: event.detail})
  }
  
  onClick(event) {
    this.move(event)
  }

  onChange(value) {
    this.player.volume = value.percent
  }
  
  onHandleMouseDown(event) {
    event.stopPropagation()
    event.preventDefault()
    this.dragging = true
    window.addEventListener("mouseup", this.onHandleMouseUp)
    window.addEventListener("mousemove", this.onHandleMouseMove)
    window.addEventListener("touchend", this.onHandleMouseUp)
    window.addEventListener("touchmove", this.onHandleMouseMove)
  }

  render() {
    if (akamai.amp.Utils.isVolumeSettable() == false)
      return null

    return super.render()
  }
}
